<template>
  <div class="flex-1 py-spacing-100 text-content-on-neutral-primary">
    <h3
      class="mb-spacing-150 text-body/l/relaxed/500 text-content-on-neutral-secondary"
    >
      General Details
    </h3>
    <div
      v-for="(data, key) in companyDetails"
      :key="key"
      class="neuxs:gap-spacing-100 mb-spacing-150 flex flex-col items-start justify-between text-body/m/relaxed/400 nexus:mb-spacing-0 nexus:flex-row ipad:text-body/l/relaxed/400"
    >
      <span
        class="text-left text-content-on-neutral-secondary nexus:text-content-on-neutral-primary"
        >{{ data?.label }}:</span
      >
      <span v-if="data?.value" class="nexus:text-right">{{ data?.value }}</span>
    </div>
  </div>
</template>

<script>
import { calculateDistanceTo } from "@/utilities/helpers/locationServices";

export default {
  props: {
    company: {
      type: Object,
      required: false,
      default: () => ({}),
    },
  },
  data() {
    return {
      userLocation: null,
      distance: null,
      isLocationLoading: false,
    };
  },
  mounted() {
    this.calculateDistance();
  },
  watch: {
    company() {
      this.calculateDistance();
    },
  },
  computed: {
    companyDetails() {
      let output = [];
      if (this.company?.genNumber) {
        output.push({
          label: "General Number",
          value: this.company?.genNumber,
        });
      }
      if (this.company?.commNumber) {
        output.push({
          label: "Commercial Number",
          value: this.company?.commNumber,
        });
      }
      if (this.company?.email) {
        output.push({ label: "Email", value: this.company?.email });
      }
      if (this.company?.location) {
        output.push({ label: "Location", value: this.company?.location });
      }
      output.push({
        label: "Distance From Your Location",
        value: this.isLocationLoading
          ? "Calculating Distance..."
          : this.distance
            ? this.$options.filters.toDistance(this.distance)
            : "Not Available",
      });
      return output;
    },
  },
  methods: {
    async calculateDistance() {
      const companyLocation = [this.company?.lat, this.company?.long];
      if (this.company?.lat && this.company?.long) {
        this.isLocationLoading = true;
        this.distance = await calculateDistanceTo(companyLocation);
        this.isLocationLoading = false;
      }
    },
  },
};
</script>
